/** map3d样式 less文件*/
.cesiumContainer {
  background: #000;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
}
.slider {
  position: absolute;
  left: 50%;
  top: 0px;
  background-color: #D3D3D3;
  width: 4px;
  height: 100%;
  z-index: 9999;
}
.slider #creditContainer {
  display: none;
}
.slider:hover {
  cursor: ew-resize;
}
